body {
  margin: 0;
  overflow: hidden;
}
.container {
  width: 100vw;
  height: 100vh;
}
.stage {
  background: blue;
  width: 100%;
  height: 100%;
}
.player {
  width: 53px;
  height: 76px;
  background: url(./images/ballerinaSprites_right.png);
  background-position: 0 100%;
  position: absolute;
  left: 0;
  top: 50vh;
}
@keyframes balerinaWalk_right {
  from {
    background-position: 0 0%;
  }
  to {
    background-position: -373px 0;
  }
}
@keyframes balerinaWalk_left {
  from {
    background-position: 100% 0%;
  }
  to {
    background-position: calc(100% + 373px) 0%;
  }
}
@keyframes balerinaJump {
  from {
    background-position: 0 50%;
  }
  to {
    background-position: -914px 50%;
  }
}

.walking_right {
  animation: balerinaWalk_right 0.6s steps(7) infinite;
  background: url(./images/ballerinaSprites_right.png);
}
.walking_left {
  background: url(./images/ballerinaSprites_left.png);
  animation: balerinaWalk_left 0.6s steps(7) infinite;
}
.jumping {
  animation: balerinaJump 0.6s steps(17) infinite;
}
